<template>
    <div>
        <van-popup v-model="show" style='background-color:transparent;font-variant: normal' :close-on-click-overlay='false'>
            <div class='poster-div'>
                <van-loading type="spinner" color="#1989fa" v-if='loading' vertical>
                     <slot>分享海报生成中</slot>
                </van-loading>
                <div v-if='!loading' class='slide-in-elliptic-top-fwd'>
                    <div class='close_box' @click="$emit('close')"><div class='close_icon'><van-icon name="cross"/></div></div> 
                    <img :src="transformPoster" alt="" class='poster_self'/>
                    <div class='tip'>长按保存海报</div>
                </div>
            </div>
        </van-popup>
    </div>
</template>
<script>
export default {
    components:{},
    props:{
        loading:{
             type:Boolean,
             default:false
        },
        transformPoster:{
            type:String
        }
    },
    watch:{
     
    },
    data() {
        return {
             
            show:true,
            qrcodeUrl:'',
           
        }
    },
    created(){
    
    },
    mounted(){
      
         
    },
    methods:{
 
    },
}
</script>
<style lang='scss' scoped> 
.close_box{
    display:flex;
    justify-content: flex-end;
    margin:0 22px 22px 0;
    .close_icon{
        width:48px;
        height:48px;
        border-radius:50%;
        text-align:center;
        line-height:48px;
        color: #fff;
        border: 4px solid #fff;
        font-size:32px;
    }
}
.tip{
    font-size: 30px;
    font-family: PingFang SC;
    font-weight: 400;
    color: #FFFFFF;
    margin:63px 0 10px 0;
    text-align:center;
}
.poster_self{
    width:612px;
    
}
.slide-in-elliptic-top-fwd{animation:slide-in-elliptic-top-fwd .7s cubic-bezier(.25,.46,.45,.94) both}
@keyframes slide-in-elliptic-top-fwd{0%{transform:translateY(-600px) rotateX(-30deg) scale(0);transform-origin:50% 100%;opacity:0}100%{transform:translateY(0) rotateX(0) scale(1);transform-origin:50% 1400px;opacity:1}}
</style>


