<template>
  <div :style="{'background-image':'url(\'' + require('@/assets/images/newKitchen/kitchen_bg.png') + '\')'}"
       class='bg-lottery'
       id='bgout'>
    <div class="top_text_box" ref='textBox' id="textBox">
      <div class='rulebox'
           @click='popRuleBox'>
        <span class='ruletext'>活动规则</span>
      </div>
      <div class="cf_text"><img :src="require('@/assets/images/newKitchen/xcftext.png')" alt=""></div>
      <div class="mh_text"><img :src="require('@/assets/images/newKitchen/xytext.png')" alt=""></div>
      <div class='people-total'>累计<span class='num'>{{totalPeople}}</span>人次参与</div>
    </div>
    <!--老虎机-->
    <div class="lhj_box" ref="lhjBox" id="lhjbox">
      <img :src="require('@/assets/images/newKitchen/cjj.png')" alt="" :style="{'height':`${homeStyle.lhjHeight}px`}" class="yx_img">
      <div class="remain_box">
          <div class='title-box'>
            <div class='remain'
                v-if='isLogined'>剩余开盒次数:<span class="num"> {{lottery.unpackingCount}} </span>次</div>
            <div class='remain'
                v-else>剩余开盒次数:<span class="num"> 1 </span>次</div>
          </div>
          <div class="red_line"></div>
      </div>
        <!--活动已开始-->
      <div class='bottom_btn_box' :style="{'top':`${homeStyle.mainBtnTopVal}px`}"
            v-if="isInActivityTime=='inner'">
          <div v-if='isLogined'>
            <!--无抽奖次数-已分享-->
            <div class='btn_self'
                @click='useAppShare'
                v-if='lottery.shareCount>0'>
              <img :src="require('@/assets/images/newKitchen/button_share2.png')"
                  alt="">
            </div>
            <!--无抽奖次数-未分享-->
            <div class='btn_self'
                @click='useAppShare'
                v-else>
              <img :src="require('@/assets/images/newKitchen/button_share1.png')"
                  alt="">
            </div>
            <!--有抽奖次数--开奖按钮-->
            <div class='btn_self'
                @click='gotoLottery' v-if='lottery.unpackingCount>0'>
              <img :src="require('@/assets/images/newKitchen/button_open.png')"
                  alt="">  
            </div>
            <div class='btn_self'
                @click='gotoLottery' v-if='lottery.unpackingCount<=0&&lottery.shareCount<=0'>
              <img  
                  :src="require('@/assets/images/newKitchen/button_unopen.png')"
                  alt="">
            </div>
          </div>
          <!-- 未登录处理 -->
          <div class='btn_self'
              @click='gotoLogin'
              v-else>
            <img :src="require('@/assets/images/newKitchen/button_open.png')"
                alt="">
          </div>
      </div>
        <!--活动未开始-->
      <div class='bottom_btn_box' :style="{'top':`${homeStyle.mainBtnTopVal}px`}"
            v-if="isInActivityTime=='before'">
          <div class='btn_self'>
            <img :src="require('@/assets/images/newKitchen/button_unstart.png')"
                alt="">
          </div>
      </div>
        <!--活动已结束-->
      <div class='bottom_btn_box' :style="{'top':`${homeStyle.mainBtnTopVal}px`}"
            v-if="isInActivityTime=='after'">
          <div class='btn_self'>
            <img :src="require('@/assets/images/newKitchen/button_finished.png')"
                alt="">
          </div>
      </div>
      <!--我的奖品-->
      <div class='my_rewardbox' @click='skipMyreward'>
        <img :src="require('@/assets/images/newKitchen/icon_prize.png')"
            class='reward_img'
            alt="">
        <div class='title'>我的奖品</div>
        <img :src="require('@/assets/images/newKitchen/icon_more.png')"
            class='reward_img_jiantou'
            alt="">
      </div>
    </div>
    <div :style="{'height':`calc(${homeStyle.blockHeight}px + env(safe-area-inset-bottom))`,'margin-top':'-2px'}" class="supply_block"></div>
    <!--规则弹框-->
    <van-popup closeable
               v-model="isShow"
               :close-on-click-overlay='false'
               round
               :style="{ height:'auto',width:'80%',padding:'38px 16px 16px'}">
      <div class='rule_title'>活动规则</div>
      <div class='rule_text'>
        <div>1、本次活动时间为2022年4月25日11时0分至2022年5月24日17时0分；</div>
        <div>2、每位用户最多可获得3次拆盲盒机会；</div>
        <div>3、中奖用户可在“中奖弹窗”或“我的奖品”页面填写收货地址，如在活动时间内未填写收货地址，则视为放弃奖品；</div>
        <div>4、为保证发货效率，收货地址一经提交不予修改；</div>
        <div>5、若平台发现用户存在批量注册、恶意刷奖等行为，平台有权取消其中奖资格且不予发货；</div>
        <div>6、本活动最终解释权归老板电器所有。</div>
      </div>
    </van-popup>
    <!--是否中奖弹框-->
    <van-popup closeable
               :close-on-click-overlay='false'
               :close-icon="require('@/assets/images/close.png')"
               v-model="rewardInfo"
               class="reward-info-bg"
               :style="{ width: '100%'}">
      <div class="reward-info slide-fwd-center"
           :style="{'background-image':'url(\'' + require('@/assets/images/newKitchen/reward-bg.png') + '\')'}">
        <div class="in-reward"
             v-if="selected">
          <p>恭喜你，中奖啦！</p>
          <div class="img">
            <img :src="rewardObj.url"
                 alt="">
          </div>
          <div class="reward-level">{{rewardObj.rankName}}</div>
          <div class="reward-name">{{rewardObj.rewardName}}</div>
        </div>
        <div class="un-reward"
             :style="{'background-image':'url(\'' + require('@/assets/images/unselected-bg.png') + '\')'}"
             v-else>
          <div class="info">
            {{encourageText}}
          </div>
        </div>
        <div class="reward-footer"
             :class='{"unselected":!selected}'>
          <div class="btn add-address"
               v-if="selected"
               @click="goEditAddress">填写收货地址领奖</div>
          <div class="btn share"
               @click='useAppShare'
               v-if='lottery.shareCount>0'>分享好友</div>
          <div class="btn share"
               @click='useAppShare'
               v-else>分享好友，拆盒次数+2</div>
        </div>
      </div>
    </van-popup>
    <!--分享h5海报-->
    <div class='mut-poster-box'
         ref='posterCut'
         id='posterCutDom'>
      <div :style="{'background-image':'url(\'' + img.bgImg + '\')'}"
           class='bg-lottery-poster'>
          <div class="top_text_box" ref='posterTextBox'>               
            <div class="cf_text"><img :src="img.titleImg" alt=""></div>
            <div class="mh_text"><img :src="img.subTitleImg" alt=""></div>
          </div>
          <div class="poster_lhj_box" ref="shareLhjBox" :style="{'height':`440px`}">
              <img :src="require('@/assets/images/newKitchen/cjj.png')" alt=""   class="yx_img">
              <div class="poster_remain_box">
                  <div class='title-box'>
                      <div class='remain'><span class="num">您有一个盲盒待开启</span></div>
                  </div>
                  <div class="red_line"></div>
              </div>
              <div class='poster-bottom'>
                  <div class='code-out'>
                      <vue-qr :text="qrcodeUrl"
                              :margin="0"
                              colorDark="#000"
                              colorLight="#fff"
                              :size="110"></vue-qr>
                  </div>
                  <div class='name'>长按识别二维码，参与抽奖</div>
              </div>
          </div>
      </div>
    </div>
    <div class="screen-bg" v-if="isLoading">
      <div class="load-box"><van-loading size="24px" color="#fff" text-color="#fff" vertical>加载中...</van-loading></div> 
    </div>
    <creat-poster v-if='displayPoster'
                  @close='displayPoster=false'
                  :transformPoster='transformPoster'
                  :loading='loading'/>
  </div>
</template>
<script>
import {
  browser,
  compareTime,
  getBase64,
  winnerList,
  encourageList,
} from '@/utils'
import creatPoster from './creatPoster'
import {
  lotteryInit,
  lotteryTotalPeople,
  shareAfterFun,
  addShareUserNote,
  clickLotteryReward,
  getSign,
} from '@/api/lotteryDraw'
import { urlLink } from '@/utils/request'
import vueQr from 'vue-qr' //生成二维码
import html2canvas from 'html2canvas';
export default {
  name: 'newKitchenDay',
  components: {
    creatPoster,
    vueQr,
  },
  data() {
    return {
      isLogined: false,
      userId: '',
      isInActivityTime: 'inner', //是否在活动期间内
      lottery: {
        unpackingCount: 1, //剩余拆盒次数
        shareCount: 0, //分享次数
      },
      totalPeople: 200, //累计参与人数
      isShow: false, //活动规则弹框
      rewardInfo: false, //中奖、未中奖弹框
      selected: false,
      displayPoster: false,
      qrcodeUrl: '',
      img: {
          titleImg: require('@/assets/images/newKitchen/xcftext.png'),
          subTitleImg:require('@/assets/images/newKitchen/xytext.png'),
          bgImg: require('@/assets/images/newKitchen/kitchen_bg.png'),
      },
      loading: false,
      transformPoster: '',
      rewardObj: {
        rank: '',
        rankName: '',
        rewardName: '',
        url: '',
      },
      encourageText: '狂吃不胖',
      fromPage: '',
      homeStyle:{
        lhjHeight:500,//老虎机的高
        mainBtnTopVal:303,
        blockHeight:10,
        //pageOverflow:'auto'
      },
      isLoading:false,
    }
  },
  beforeRouteEnter(to, from, next) {
    console.log('from', from)
    const page = from.name
    next((vm) => {
      vm.fromPage = page
    })
  },
  activated() {
    console.log('this.isLogined', this.isLogined,'this.$route.query----',this.$route.query)
    document.documentElement.style.height = '100%'
    document.body.style.height = '100%'
    const { terminal,userId } = this.$route.query;
    const isGeneratePoster=terminal != 'app'&&!this.isLogined?true:false;
    this.initStatus();
    if(isGeneratePoster){
       this.generatePoster() //供h5分享海报使用
    }
    console.log('this.isLogined', this.isLogined)
    if (this.rewardInfo) {
      this.rewardInfo = false
    }
  },
  created() {
  },
  mounted() {
    let me = this
    window['skipReward'] = (userId) => {
      me.skipReward(userId) // 这个也就是我定义的方法
    }
    window['refreshMarchActivity'] = (userId) => {
      me.refreshMarchActivity(userId) // 这个也就是我定义的方法
    }
    window['shareFinish'] = () => {
      me.appShareFinish() // 这个也就是我定义的方法
    }
    this.initStyleFun();
  },
  methods: {
    handleResize(){
      console.log('窗口变化了-----')
    },
    //初始化样式
    initStyleFun(){
      console.log('navigator----',navigator)
      let _this=this;
      try{
        const totalHeight=window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;;
        const screenWidth=document.body.clientWidth; 
        const lhjHeight=(screenWidth*1790)/1500;//图片像素比，1500宽，2348高
        this.homeStyle.lhjHeight=lhjHeight;
      _this.$nextTick(()=>{
           console.log('手机屏幕高度---',totalHeight,'---老虎机高度盒子-iiiii--',this.$refs.lhjBox.offsetHeight,'lhjHeight--大图片----',lhjHeight)
           console.log('this.$refs.textBox.offsetHeight---上方文本高--',this.$refs.textBox.offsetHeight)
           const textBoxHeight=this.$refs.textBox.offsetHeight;//上方文本盒子
           const mainHeight=this.$refs.lhjBox.offsetHeight;
           var blockHeight=10;
           if((textBoxHeight+mainHeight+10)<totalHeight){
             console.log('小于屏幕高')
             blockHeight=totalHeight-textBoxHeight-mainHeight;
              //_this.homeStyle.pageOverflow='hidden'
           } 
           _this.homeStyle.mainBtnTopVal=((610*screenWidth)/750).toFixed(2);
           _this.homeStyle.blockHeight=blockHeight+2
          console.log('blockHeight',blockHeight,_this.homeStyle.pageOverflow,'抽奖按钮-----mainBtnDiv----',_this.homeStyle.mainBtnTopVal);  
        })
      }catch(err){

      }
      
    },
    //初始化登录态
    async initStatus() {
      const { userId, shareUserId, terminal } = this.$route.query
      localStorage.setItem('terminal', terminal == 'app' ? 'app' : '');
      if(terminal=='app'){
        if (userId&& userId !== 'null'&&userId !== 'undefined') {
          this.userId  = userId
          this.isLogined = true
        }else{
          this.userId  = ''
          this.isLogined = false
        }
      }else{
        const userInfo = JSON.parse(localStorage.getItem('userInfoke'));
        console.log('userInfo----888---',userInfo)
        if (userInfo && userInfo.id) {
          this.userId = userInfo.id
          this.isLogined = true
        }else{
           this.userId =''
           this.isLogined = false
        }
      }
      this.isInActivityTime = compareTime(
        '2022-04-25 11:00:00',
        '2022-05-24 17:00:00'
      )
      //如果有分享者id，后端给分享者记录数据
      if (shareUserId) {
        const { sign, timestamp } = this.$route.query
        const vData = await addShareUserNote({
          shareUserId,
          sign,
          timestamp,
          userId: undefined,
        }).catch((err) => {
          console.log('err--签名---错误---', err)
        })
      }
      this.getLotteryInfo() //初始化抽奖信息
    },
    //获取用户初始化抽奖信息
    getLotteryInfo() {
      //获取页面累计人数
      lotteryTotalPeople().then((res) => {
        this.totalPeople = res.data ? res.data : 200 //200为虚拟参与人数
      })
      if (this.isLogined) {
        //获取剩余拆盒次数和分享次数
        this.isLoading=true;
        lotteryInit({ userId: this.userId, shareUserId: undefined }).then(
          (res) => {
            this.isLoading=false;
            if (res.data) {
              const { unpackingCount, shareCount } = res.data
              this.lottery = {
                unpackingCount: unpackingCount ? unpackingCount : 0, //剩余拆盒次数
                shareCount: shareCount ? shareCount : 0, //分享次数
              }
            }
          }
        ).catch(err=>{
          this.isLoading=false;
        })
      }
    },

    //点击跳转我的奖品
    skipMyreward() {
      const { terminal } = this.$route.query
      if (this.isLogined) {
        this.$router.push(`/myRewards/${this.userId}?terminal=${terminal}`)
        return
      }
      //未登录处理
      const { isWeixin, isWeibo, isQQSpace, iPhone, iPad, iPod, android } =
        browser.versions
      if (isWeixin || isWeibo || isQQSpace) {
        this.$router.push(`/login?lastLabel=goReward`)
      } else if ((iPhone || iPad || iPod) && terminal == 'app') {
        //ios端APP跳转
        try {
          //登陆成功ios跳转我的奖品页面
          window.webkit.messageHandlers.toIosLoginPage.postMessage(1)
        } catch (error) {
          console.log('ios Error')
        }
      } else if (android && terminal == 'app') {
        //安卓端app跳转
        try {
          RecipeDetail.toAndroidLoginPage(1) //登陆成功安卓跳转我的奖品页面
        } catch (error) {
          console.log('Android Error')
        }
      } else {
        this.$router.push(`/login?lastLabel=goReward`)
      }
    },
    //抽奖未登录时--跳转登录
    gotoLogin() {
      const { terminal } = this.$route.query
      const { isWeixin, isWeibo, isQQSpace, iPhone, iPad, iPod, android } =
        browser.versions
      if (isWeixin || isWeibo || isQQSpace) {
        this.$router.push(`/login?lastLabel=backLottery`)
      } else if ((iPhone || iPad || iPod) && terminal == 'app') {
        //ios端APP跳转登录
        try {   
          window.webkit.messageHandlers.toIosLoginPage.postMessage(2)
        } catch (error) {
          console.log('ios Error')
        }
      } else if (android && terminal == 'app') {
        //安卓端app跳转
        try {
          RecipeDetail.toAndroidLoginPage(2) //登陆成功需要安卓刷新抽奖页
        } catch (error) {
          console.log('Android Error')
        }
      } else {
        this.$router.push(`/login?lastLabel=backLottery`)
      }
    },
    //已登录--点击抽盲盒
    gotoLottery() {
      if (this.isLogined) {
        if (this.lottery.unpackingCount <= 0) {
          return this.$toast('您没有开盒次数，\n立即分享,开盒次数+2!')
        }
        //调用抽奖接口
        clickLotteryReward({ userId: this.userId }).then((res) => {
          console.log('res', res)
          if (res.data) {
            const { prizeId, shareCount, unpackingCount } = res.data
            this.lottery = {
              unpackingCount: unpackingCount ? unpackingCount : 0, //剩余拆盒次数
              shareCount: shareCount ? shareCount : 0, //分享次数
            }
            const prizeArr = [6, 7, 8, 9]
            if (prizeArr.indexOf(prizeId) > -1) {
              this.selected = true
              this.rewardObj = winnerList.find((item) => item.rank == prizeId)
            } else {
              this.selected = false
              const num = parseInt(Math.random() * 9)
              this.encourageText = encourageList.find(
                (val, index) => index === num
              )
            }
            console.log('selected',this.selected)
            this.rewardInfo = true
          }
        })
      }
    },
    //唤起分享---已是登录状态
    useAppShare() {
      const { isWeixin, isWeibo, isQQSpace, iPhone, iPad, iPod, android } =
        browser.versions
      const { terminal } = this.$route.query
      if (isWeixin || isWeibo || isQQSpace) {
        //生成海报
        this.displayPoster = true
        if (this.rewardInfo) {
          this.rewardInfo = false
        }
      } else if ((iPhone || iPad || iPod) && terminal == 'app') {
        //ios端APP拉起分享
        try {
          window.webkit.messageHandlers.iosShareLayer.postMessage(null)
        } catch (error) {
          console.log('ios Error')
        }
      } else if (android && terminal == 'app') {
        //安卓端app拉起分享
        try {
          RecipeDetail.androidShareLayer() //分享成功安卓重载页面
        } catch (error) {
          console.log('Android Error')
        }
      } else {
        //生成海报
        this.displayPoster = true
        if (this.rewardInfo) {
          this.rewardInfo = false
        }
      }
    },
    //从我的奖品到登录页登陆成功后app端调用-要注册到全局
    skipReward(userId) {
      //链接添加userId
      let newQuery = JSON.parse(JSON.stringify(this.$route.query))
      newQuery.userId = userId
      this.$router.replace({
        query: newQuery,
      })
      this.isLogined = true
      this.userId = userId
      console.log(
        'this.$route.query',
        this.$route.query,
        'localStorage.getItem(terminal)',
        localStorage.getItem('terminal')
      )
      localStorage.setItem('terminal', 'app')
      this.$router.push(`/myRewards/${userId}?terminal=app`)
    },
    //抽盲盒未登录跳app登录页后刷新h5页面-要注册到全局
    refreshMarchActivity(userId) {
      //初始化按钮状态
      let newQuery = JSON.parse(JSON.stringify(this.$route.query))
      newQuery.userId = userId
      this.$router.replace({
        query: newQuery,
      })
      this.isLogined = true
      this.userId = userId
      console.log(
        'this.$route.query',
        this.$route.query,
        'localStorage.getItem(terminal)',
        localStorage.getItem('terminal')
      )
      localStorage.setItem('terminal', 'app')
      this.getLotteryInfo()
      if (this.rewardInfo) {
        this.rewardInfo = false
      }
    },
    //app端分享完之后-调用h5方法，后端记录分享次数
    appShareFinish() {
      const params = {
        shareUserId: undefined,
        userId: this.userId,
      }
      shareAfterFun(params).then((res) => {
        if (res.data) {
          const { unpackingCount, shareCount } = res.data
          this.lottery = {
            unpackingCount: unpackingCount ? unpackingCount : 0, //剩余拆盒次数
            shareCount: shareCount ? shareCount : 0, //分享次数
          }
        }
      })
      if (this.rewardInfo) {
        this.rewardInfo = false
      }
    },
    popRuleBox() {
      this.isShow = true
    },
    //跳转收货地址页面
    goEditAddress() {
      this.$router.push(`/address/${this.userId}`)
    },
    //生成h5海报
    generatePoster() {
      if (this.isLogined) {
        const timestamp = Date.now()
        getSign({ shareUserId: this.userId, timestamp })
          .then((res) => {
            //console.log('res---获取签名',res);
            if (res.code == 200) {
              const sign = res.data
              this.qrcodeUrl = `${urlLink}/lotteryDraw?shareUserId=${
                this.userId
              }&timestamp=${timestamp}&sign=${encodeURIComponent(sign)}`
              //console.log('this.qrcodeUrl',this.qrcodeUrl)
              this.imgToBase()
              this.$nextTick(() => {
                this.transformFun()
              })
            }
          })
          .catch((err) => {
            console.log('获取签名错误')
            this.qrcodeUrl = `${urlLink}/lotteryDraw?shareUserId=${this.userId}`
            //console.log('this.qrcodeUrl',this.qrcodeUrl)
            this.imgToBase()
            this.$nextTick(() => {
              this.transformFun()
            })
          })
      }
    },

    //海报上的图片转换成base64
    async imgToBase() {
      let titleImg = await getBase64(this.img.titleImg), //标题图片
      subTitleImg=await getBase64(this.img.subTitleImg),
        bgImg = await getBase64(this.img.bgImg)
      this.img = {
        titleImg,
        subTitleImg,
        bgImg,
      }
    },
    //html2canvas转化成分享海报
    transformFun() {
      const { terminal } = this.$route.query
      if (!terminal || terminal != 'app') {
        this.loading = true
        //设置放大倍数
        // const scale = window.devicePixelRatio;
        const scale = 2
        const width = this.$refs.posterCut.outerWidth
        const height = this.$refs.posterCut.outerHeight
        console.log('width',width,'height',height,this.$refs.posterCut.offsetHeight);
        // html2canvas配置项
        const ops = {
          scale: 2,
          width: 374,
          height: 597,
          useCORS: true,
          allowTaint: false,
          dpi: window.devicePixelRatio * 2,
        }
        setTimeout(() => {
          html2canvas(this.$refs.posterCut, ops).then((canvas) => {
            let dataURL = canvas.toDataURL('image/png')
            //console.log('dataURL',dataURL);
            this.loading = false
            this.transformPoster = dataURL
            // var box = document.getElementById('posterCutDom')
            // box.remove()
          })
        }, 500)
      }
    },
  },
}
</script>
